.tag {
  background-color: #f4f4f4;
  border: 1px solid #e9e9e9;
  padding: 2px 0 2px 2px;
  border-radius: 2px;
  display: inline-block;
}
.tag:focus-within {
  background-color: #e9e9e9;
  border-color: #a0a0a0;
}
.tag-remove {
  padding: 2px 5px;
  color: #a0a0a0;
  line-height: 100%;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
}
.tag-remove.disabled,
.tag-remove.readOnly {
  cursor: not-allowed;
}
.tag-remove:focus {
  color: #3c3c3c;
}
.node > label {
  cursor: pointer;
  margin-left: 0.125rem;
  vertical-align: bottom;
}
.tag-list {
  display: inline;
  padding: 0;
  margin: 0;
}
.tag-item {
  display: inline-block;
  margin: 0.125rem 0.25rem;
}
.tag-item .search {
  border: none;
  outline: none;
}
.tag-item input {
  padding: 0.3rem;
}
.tag-item:last-child {
  margin-right: 0.25rem;
}
.hide:not(.match-in-children) {
  display: none;
}
.react-dropdown-tree-select .dropdown {
  position: relative;
  display: table;
  width: 100%;
}
.react-dropdown-tree-select .dropdown .dropdown-trigger {
  line-height: 20px;
  max-height: 13rem;
  width: 100%;
  margin: 0.1rem;
  display: inline-block;
  overflow: auto;
  border: 1px solid #939393;
  background: white;
  border-radius: 0.25rem;
}
.react-dropdown-tree-select .dropdown .dropdown-trigger.arrow {
  cursor: pointer;
}
.react-dropdown-tree-select .dropdown .dropdown-trigger.arrow.bottom:after {
  content: '\25BC';
  color: #ededed;
  position: absolute;
  right: 0;
  bottom: 0.5rem;
  padding: 0.5rem 0.6rem;
}
.react-dropdown-tree-select .dropdown .dropdown-trigger.arrow.top:after {
  content: '\25B2';
  color: #ededed;
  position: absolute;
  right: 0;
  bottom: 0.5rem;
  padding: 0.5rem 0.6rem;
}
.react-dropdown-tree-select .dropdown .dropdown-trigger.arrow.disabled {
  cursor: not-allowed;
}
.react-dropdown-tree-select
  .dropdown
  .dropdown-trigger.arrow.disabled.bottom:after {
  color: #939393;
}
.react-dropdown-tree-select .dropdown .dropdown-content {
  position: absolute;
  padding: 0.25rem;
  z-index: 1;
  background: #fff;
  border: 1px solid #ededed;
  border-radius: 0.5rem;
  -webkit-box-shadow: 0 5px 8px rgba(0, 0, 0, 0.15);
  box-shadow: 0 5px 8px rgba(0, 0, 0, 0.15);
}
.react-dropdown-tree-select .dropdown .dropdown-content ul {
  margin: 0;
  padding: 0;
}
.node {
  list-style: none;
  white-space: nowrap;
  padding: 0.25rem;
}
.node.leaf.collapsed {
  display: none;
}
.node.disabled > * {
  color: gray;
  cursor: not-allowed;
}
.node.match-in-children.hide .node-label {
  opacity: 0.5;
}
.node.focused {
  background-color: #f4f4f4;
}
.toggle {
  white-space: pre;
  display: inline-block;
  margin-right: 0.25rem;
  width: 2.25rem;
  height: 2.25rem;
  background: #939393;
  border-radius: 0.5rem;
  cursor: pointer;
  text-align: center;
  vertical-align: top;
  color: white;
  font-size: 2rem;
  font-style: normal;
  overflow: hidden;
}
.toggle:after {
  content: ' ';
}
.toggle.collapsed:after {
  content: '\203A';
  position: relative;
  top: -0.4rem;
}
.toggle.expanded:after {
  content: '\2303';
  position: relative;
  top: 0.2rem;
}
.searchModeOn .toggle {
  display: none;
}
.checkbox-item,
.radio-item {
  vertical-align: middle;
  margin: 0 0.25rem 0.25rem 0;
  width: 1.8rem;
  height: 1.8rem;
}
.checkbox-item.simple-select,
.radio-item.simple-select {
  display: none;
}

